import { useState } from "react";
import styled from "styled-components";
// import LocalLoader from "./../LocalLoader/index";

const ImageBox = styled.div`
  width: 100%;
  height: 100%;
  background-color: #1b2735;
  position: relative;
  > div {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
  }
  > img {
    opacity: ${({ loading }) => (loading ? "0" : "1")};
    visibility: ${({ loading }) => (loading ? "hidden" : "visible")};
  }
`;

const CloseIcon = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  height: 50px;
  cursor: pointer;
  transition: 0.3s;
  z-index: 10;
  background-image: url("data:image/svg+xml;base64,");
  background-color: #141e2a;
  border-radius: 0 0 0 4px;
  background-size: 30px;
  background-repeat: no-repeat;
  background-position: center;
`;

const Img = styled.div`
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
  img {
    max-height: 80vw;
    opacity: ${({ loading }) => (loading ? "0" : "1")};
    visibility: ${({ loading }) => (loading ? "hidden" : "visible")};
  }
  ${({ change }) =>
    change &&
    `
  &::before {
    position: absolute;
    opacity: 0;
    transition: 0.3s;
    content: "";
    left: 45%;
    top: 45%;
    cursor: pointer;
    width: 10%;
    height: 10%;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 5;
    border-radius: 50%;
    background-image: url("data:image/svg+xml;base64,");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 40px;
  }
  &:hover::before {
    opacity: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border-radius: 0;
  }`}
`;

export default function Image({ big, src, handleClickBig, change }) {
  const [loading, setLoading] = useState(1);

  const [open, setOpen] = useState(false);
  const handleClose = () => {
    setOpen(false);
  };
  const handleOpen = () => {
    if (big) {
      setOpen(true);
    }
  };

  return (
    <ImageBox loading={loading}>
      {/* {!!loading && <LocalLoader />} */}
      <img alt="" onLoad={() => setLoading(0)} src={src} onClick={handleOpen} />
      {/* {big && (
        <Dialog
          open={open}
          onClose={handleClose}
          classes={{
            paperWidthSm: classes.paperWidthSm,
          }}
        >
          <CloseIcon onClick={handleClose} />
          <Img
            change={change}
            onClick={() => {
              handleClose();
              if (change) handleClickBig();
            }}
          >
            {!!loading && <LocalLoader />}
            <img alt="" onLoad={() => setLoading(0)} src={src} />
          </Img>
        </Dialog>
      )} */}
    </ImageBox>
  );
}
